<template>
  <div> 
    <div class="shadowback" v-if="isDisplay"></div>
    <div class="container" @click="handleClick" @keyup.e="emitKeyEvent" tabindex="0" @keyup.r="clickKq1"
    @keydown.w.prevent="moveUp"
       @keydown.s.prevent="moveDown" @keydown.a.prevent="moveLeft"
       @keydown.d.prevent="moveRight"
       ref="container"
       > 
      <audio ref="audioPlayer"></audio>
      <audio ref="zhanjiAudioPlayer"></audio>
      <p class="p1">这里是小小刻晴</p>
      <div id="subtitle-seperator"></div>
      <p class="p2">wasd控制移动,单击鼠标释放雷楔,雷楔存在时按e使用元素战技也可以双击鼠标左键,点击刻晴或按q释放元素爆发</p>
    <a-tooltip trigger="click" :visible="showToolTip" >
    <template slot="title">
      {{ text[textindex] }}
    </template>
      <img 
        :id="id"  
        class="kq1"  
        @click.stop="clickKq1"   
        :src="src"    
        v-show="!isDisplay"   
        ref="kq1"  
      />
    </a-tooltip>
      <img  
        :src="boomSrc"    
        :key="kq2Key"
        class="kq2"
        v-if="isDisplay"  
        style="width: 128px;"    
        ref="kq2" 
      />    
      <img src="../assets/keimg/leixie.gif" alt="" class="leixie" ref="leixie" v-show="showleixie">
    </div>
  </div>
  </template>
  
  <script>
import { watch } from 'vue'
  export default {
    data () {
      return {
        id: 1,
        src: require('../assets/keimg/weapon.gif'),
        boomSrc: require('../assets/keimg/yuansubaofa.gif'),   
        isDisplay: false ,
        kq2Key:0,
        text:['休息一下','这没什么难的','能量不足,滴嘟滴嘟','我是刻晴，璃月七星中的「玉衡」','cd中','劳逸结合是不错，但也别放松过头','心有所向，日复一日，必有精进','继续前进吧'],
        textindex:0,
        showToolTip:false,
        musicFiles: [
          {
            name:'元素爆发其一',
            src:'yuansubaofa1.mp3'
          },
          {
            name:'元素爆发其二',
            src:'yuansubaofa2.mp3'
          },
           {
            name:'元素爆发其三',
            src:'yuansubaofa3.mp3'
          },
        ],
        zhanjiAudio:[
        {
            name:'元素战技其一',
            src:'yuansuzhanji1.mp3'
          },
          {
            name:'元素战技其二·',
            src:'yuansuzhanji2.mp3'
          },
          {
            name:'元素战技其三',
            src:'yuansuzhanji3.mp3'
          },
          {
            name:'元素战技其四',
            src:'yuansuzhanji4.mp3'
          },
          {
            name:'元素战技其五',
            src:'yuansuzhanji5.mp3'
          },
           
        ],
        currentMusicIndex: 0,
        baoFaCurrentIndex: 0,
        isPlaying: false,
        currentTime: 0, 
        lastClickTime:0,
        lastClickKqTime:0,
        ischange:0,
        showleixie:false,
        clearleixie:false,
        leixietimerId:''
      }
    },
    methods: {
        //延时任务
        async sleep (ms) {
        return new Promise(resolve => setTimeout(resolve, ms))  
        },  
        //刻晴变大
        async startTimer () {
          this.isDisplay=false 
          // console.log("触发技能1")
          //消除单击刻晴事件雷楔出现影响
          this.clearleixie=true
        const img = document.getElementById(this.id)      
        if (img.classList.contains('enlarged')) {
            img.classList.remove('enlarged')  
        }
        img.classList.add('enlarged')  
        await  this.sleep(1200) 
        this.isDisplay=true
        this.boomSrc='yuansubaofa.gif'
        // console.log("触发技能2")
        //恢复默认设置,正常单击事件雷楔能显示
        this.clearleixie=false
        img.classList.remove('enlarged') 
        await  this.sleep(500) 
        this.playBaoFaNext("audioPlayer",this.musicFiles)
        await  this.sleep(4300)
        this.isDisplay=false 
      },
      clickKq1(){
        this.destroyGif()
         // 获取当前时间
         this.kq2Key++ ;
        //  console.log(this.kq2Key)
       const now = new Date().getTime();
      // 判断时间间隔是否小于 8000 毫秒
      if (now - this.lastClickKqTime > 20000) {
         this.lastClickKqTime=now
         this.showToolTip = false;
         this.startTimer()
      }else{
        this.textindex=(this.textindex+1)%(this.text.length)
        this.showToolTip=true;
        setTimeout(() => {
          this.showToolTip = false;
        }, 3000); // 3秒后隐藏Popover
      }
      },
      playMusic(srcname,musicFiles) {
        const audio = this.$refs[srcname]
        audio.src = musicFiles[this.currentMusicIndex].src
        audio.currentTime = this.currentTime
        this.musicname=musicFiles[this.currentMusicIndex].name
        audio.play()
        this.musicClass='fas fa-music fa-spin fa-lg'
        this.isPlaying = true
      },
      playBaoFaMusic(srcname,musicFiles) {
        const audio = this.$refs[srcname]
        audio.src = musicFiles[this.baoFaCurrentIndex].src
        audio.currentTime = this.currentTime
        this.musicname=musicFiles[this.baoFaCurrentIndex].name
        audio.play()
        this.musicClass='fas fa-music fa-spin fa-lg'
        this.isPlaying = true
      },
      playBaoFaNext(srcname,musicFiles) {
        this.baoFaCurrentIndex = (this.baoFaCurrentIndex + 1) % musicFiles.length
        this.currentTime=0
        this.iconClsss='far fa-pause-circle fa-lg'
        this.playBaoFaMusic(srcname,musicFiles)
      }, 
      plaint(startx,starty,endx,endy){
              // 记录起点和终点坐标
        const startX = startx     
        const startY = starty      
        const endX = endx             
        const endY = endy           
        
        // 计算移动路径上的点的坐标
        const path = []
        const count = 30  

        for (let i = 0; i < count; i++) {
          const x = startX + (endX - startX) * (i / count)
          const y = startY + (endY - startY) * (i / count)
          path.push({ x, y })
        }
        
        // 在每个路径点创建粒子特效
        let delay = 5;
        path.forEach( point => {
          setTimeout(() => {
            const particle = document.createElement('div');
            particle.className = 'particle';
            particle.style.left = point.x + 'px';
            particle.style.top = point.y + 'px';
            this.$refs.container.appendChild(particle);
            // 延时删除粒子特效
            setTimeout(() => {
              this.$refs.container.removeChild(particle);
            }, 900);
          }, delay); // 每个粒子的生成时间间隔为 100 毫秒
          delay=delay+7
        })
        
      },
      destroyGif() {
        if (this.$refs.kq2) {
          this.$refs.kq2.remove()
        }
      },
      playNext(srcname,musicFiles) {
        this.currentMusicIndex = (this.currentMusicIndex + 1) % musicFiles.length
        this.currentTime=0
        this.iconClsss='far fa-pause-circle fa-lg'
        this.playMusic(srcname,musicFiles)
      }, 
      //移动刻晴
      moveElement(event) {
        // this.plaint()
        // 获取需要移动的元素
      var element1 = this.$refs.kq1;
      var rect = element1.getBoundingClientRect();
        //播放战技
        this.playNext("zhanjiAudioPlayer",this.zhanjiAudio)
      // 获取鼠标点击的位置
      var x = event.clientX;
      var y = event.clientY;
       this.plaint(rect.left,rect.top,x+10,y+20)
       this.changedirect(rect.left,x)
        // console.log(x)
      //设置元素的位置为鼠标点击的位置
      this.showToolTip=false
      element1.style.left = x + 'px';
      element1.style.top = y + 'px';
    },
    //移动雷楔
    moveleixie(event){
      if (this.leixietimerId) {
        clearTimeout(this.leixietimerId);
      }
      // console.log("触发雷楔"+this.isDisplay)
      if(this.clearleixie){
        return;
      }
      this.showleixie=false;
      var x = event.clientX;
      var y = event.clientY;
      var element2 = this.$refs.leixie;
      element2.style.left = x + 'px';
      element2.style.top = y + 'px';
      this.showleixie=true;
      var element1 = this.$refs.kq1;
      var rect = element1.getBoundingClientRect();
      this.changedirect(rect.left,x)
      this.leixietimerId =  setTimeout(() =>{  
        this.showleixie=false;
      }, 3000);
    },
    handleClick(event) {
      // console.log(event.button)
      //移动雷楔
      if(event.button==0){
        this.moveleixie(event);
      }
      if(event.button==2&&this.showleixie           ){
        this.pointClick;
      }
       // 获取当前时间
       const now = new Date().getTime();
      // 判断时间间隔是否小于 300 毫秒（即双击事件）
      if (now - this.lastClickTime < 300) {
        this.moveElement(event);
        this.showleixie=false;
      }

      this.lastClickTime = now;
    },
    emitKeyEvent(event){
      if(!this.showleixie){
        return;
      }
      // console.log("jin")
       //播放战技
       this.playNext("zhanjiAudioPlayer",this.zhanjiAudio)
      // 获取雷楔位置
      var element1 = this.$refs.leixie;
      var x = element1.style.left
      var y = element1.style.top
      var rect = element1.getBoundingClientRect();
      
      // console.log(x)
      //设置元素的位置为雷楔的位置
      //移动刻晴
      this.showToolTip=false
      var element2 = this.$refs.kq1;
      var rect2 = element2.getBoundingClientRect();
      this.plaint(rect2.left,rect2.top,rect.left+10,rect.top+20)
      this.changedirect(rect2.left,rect.left)
      //设置元素的位置为鼠标点击的位置
      element2.style.left = x ;
      element2.style.top = y ;
    },
    //改变刻晴朝向
    changedirect(oldX,newX){
      if(oldX<newX){
        this.src='weaponright.gif'
      }else{
        this.src='weapon.gif'
      }
    },
    //定时变装
    changeSrc(){
      setInterval(() =>{  
        // console.log(this.src)
         if(this.src==='/img/weapon.05a06542.gif'||this.src==='weapon.gif'){
          this.src='clothes.gif';
        }
        // console.log(this.src)
        // console.log("变装")
        if(this.src==='weaponright.gif'){
        this.src='clothesright.gif';
      }
          //获取时间戳,watch监视
          this.ischange=new Date().getTime();
      }, 15000);
    },
    moveUp() {
      this.moveBox(0, -5)
      this.showToolTip = false;
    },
    moveDown() {
      this.moveBox(0, 5)
      this.showToolTip = false;
    },
    moveLeft() {
      this.moveBox(-5, 0)
      this.showToolTip = false;
      this.src='weapon.gif'
    },
    moveRight() {
      this.moveBox(5, 0)
      this.showToolTip = false;
      this.src='weaponright.gif'
    },
    moveBox(x, y) {
      const box = this.$refs.kq1
      const container = this.$refs.container
      const boxRect = box.getBoundingClientRect()
      const containerRect = container.getBoundingClientRect()
      const newX = boxRect.left + x
      const newY = boxRect.top + y
      if (newX >= containerRect.left && newX + boxRect.width <= containerRect.right 
        && newY >= containerRect.top && newY + boxRect.height <= containerRect.bottom) {
        box.style.left = newX + 'px'
        box.style.top = newY + 'px'
      }
    }
    },
    mounted(){
      //定时变装
       this.changeSrc();
       this.$refs.container.focus()
    },
    watch:{
      //刻晴变装后变回去
       async ischange(newValue, oldValue){
        if(newValue){
          //发现变装,变装完毕,恢复默认
          await  this.sleep(7000)
          if(this.src==='clothes.gif'){
          this.src='weapon.gif'
          
        }
          if(this.src==='clothesright.gif'){
          this.src='weaponright.gif'
        }
        }
      },
      isDisplay() { 
        this.kq2Key++  
        // console.log(this.kq2Key)
      }
    }
  }
  </script>
<style>
    .main{
      height: 100%;
    }
    .container{
      cursor: url('../assets/keimg/pointer.png'),default;
      display: flex;
      align-items: center;
      height: 100vh;
      justify-content: center;
      background-color: #9d88d3;
      z-index: -1;
    }
    .shadowback{
      width: 100%;
      display: inline-block;
      height: 100vh;
      position: absolute;
      background-color: black;
      opacity: 1;
      z-index: 10;
      animation: shadow-animation 1s ease-out;
    }
    .kq1{
        position: absolute;
        transition: transform 1s; /* 添加过渡 */
        cursor: pointer;
        z-index: 1;
    }
    .kq2{
        position: absolute;
        transition: transform 1s; /* 添加过渡 */
        z-index: 100;
        cursor: pointer;
    }
    .enlarged {
        transform: scale(4); /* 放大 4 倍 */
    }
    .enlargedend {
        transform: scale(1); /* 缩小为原来大小 */
    }
    .p1{
      text-align: center;
    font-size: 300%;
    font-weight: bold;
      position: absolute;
      top: 0;
      color: white;
      margin-block-start: 0.67em;
    margin-block-end: 0.67em;
    margin-inline-start: 0px;
    margin-inline-end: 0px;
    user-select:none;
    display: inline;
    }
    .p2{
      text-align: center;
    font-weight: bold;
    font-size: 2em;
      position: absolute;
      top: 105px;
      color: white;
    }
    #subtitle-seperator {
      position: absolute;
      top: 92px;
    margin-top: 6px;
    margin-bottom: 12px;
    border-bottom: 6px dashed white;
    border-top: none;
    border-left: none;
    border-right: none;
    z-index: 0;
    width: 70%;
}
.leixie{
  position: absolute;
  cursor: pointer;
  z-index: 0;
}
.particle {
  position: absolute;
  width: 5px;
  height: 5px;
  border-radius: 50%;
  background: #5d3bca;
  animation: particle-animation 1s ease-out;
}
.ant-tooltip-inner {
    min-width: 30px;
    min-height: 32px;
    padding: 6px 8px;
    color: #5d3bca;
    text-align: left;
    text-decoration: none;
    word-wrap: break-word;
    background-color: rgba(251, 251, 251, 0.75);
    border-radius: 4px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.15);
}
.ant-tooltip-arrow::before {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    display: block;
    width: 5px;
    height: 5px;
    margin: auto;
    background-color:rgba(251, 251, 251, 0.75);
    content: '';
    pointer-events: auto;
}
@keyframes particle-animation {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  } 
}
@keyframes shadow-animation {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  } 
}
</style>