<template>
  <canvas ref="canvas" style="width: 100%; height: 100%;"></canvas>
</template>

<script>
export default {
  name:'Test',
  mounted() {
    this.drawParticles()
  },
  methods: {
    drawParticles() {
      const canvas = this.$refs.canvas
      const ctx = canvas.getContext('2d')

      const startPoint = { x: 50, y: 50 } // 第一个点的坐标
      const endPoint = { x: 200, y: 150 } // 第二个点的坐标
      const particleCount = 50 // 粒子数量
      const particles = []

      for (let i = 0; i < particleCount; i++) {
        particles.push({
          x: startPoint.x,
          y: startPoint.y,
          vx: (Math.random() * 2 - 1) * 2,
          vy: (Math.random() * 2 - 1) * 2,
          size: Math.random() * 4,
          color: '#ff0000',
        })
      }

      function draw() {
        ctx.clearRect(0, 0, canvas.width, canvas.height)

        particles.forEach(particle => {
          ctx.beginPath()
          ctx.arc(particle.x, particle.y, particle.size, 0, Math.PI * 2)
          ctx.fillStyle = particle.color
          ctx.fill()

          particle.x += particle.vx
          particle.y += particle.vy

          if (particle.x < 0 || particle.x > canvas.width || particle.y < 0 || particle.y > canvas.height) {
            particle.x = startPoint.x
            particle.y = startPoint.y
          }

          const dx = endPoint.x - particle.x
          const dy = endPoint.y - particle.y
          const distance = Math.sqrt(dx * dx + dy * dy)

          if (distance < 50) {
            particle.vx = dx / distance * 3
            particle.vy = dy / distance * 3
          } else {
            particle.vx += (Math.random() * 2 - 1) * 0.1
            particle.vy += (Math.random() * 2 - 1) * 0.1
          }
        })

        ctx.beginPath()
        ctx.moveTo(startPoint.x, startPoint.y)
        ctx.lineTo(endPoint.x, endPoint.y)
        ctx.strokeStyle = '#ff0000'
        ctx.stroke()

        requestAnimationFrame(draw)
      }

      draw()
    }
  }
}
</script>