 
import Vue from 'vue'   //引入Vue
import Router from 'vue-router'  //引入vue-router
import home from '../components/Home.vue'
import Test from '../components/Test'
Vue.use(Router)  //Vue全局使用Router
 
export default new Router({
  mode:'history',
  base: '/minnikeqing/',
  routes: [              //配置路由，这里是个数组
    {
        path:'/',
        component:home,
        children:[{
          path:'test',
          component:Test
        }]
    },
  ]
})